body{
    background-color:aqua;
}
.App{
    background-color:blueviolet;
    min-height:500px;
    width:400px;
    margin:40px auto;
}
#to-do-form input{
    background-color:azure;
    border:0;
    width:200px;
    height:50px;
    padding: 0 20px;
    margin:20px;
    font-size:18px;
    border-radius:1px;
    color:black;
}
#to-do-form input::placeholder {
    color: rgba(225,225,225,0.5);
 }
    #to-do-form input:focus, #to-do-form button:focus {
        outline: none;
    }
 #to-do-form button{
     height:50px;
     width:80px;
     border:0;
     border-radius:5px;
     background-color:bisque;
     cursor:pointer;
     font-weight:bold;
     color:black;
 }